.root {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  flex-flow: column;
  flex-grow: 1;
  overflow: hidden;

  .header {
    min-height: 40px;
    display: flex;
    justify-content: space-between;
    background-color: var(--bg-primary-color);
  }
  
  .fileTabs {
    display: flex;
  }
  
  .tab {
    padding: 5px 15px;
    display: flex;
    justify-content: space-between;
    border-top: 2px solid transparent;

    &:hover {
      background-color: var(--bg-secondary-color);
      cursor: pointer;

      .icon {
        visibility: visible;
      }
    }

    &:active {
      background-color: var(--bg-color);
    }

    &.selected {
      border-top: 2px solid #3ba3cc;
      background-color: var(--bg-color);

      .name {
        color: var(--primary-color);
      }
    }

    .languageIcon {
      padding-right: 5px;
    }

    .name {
      color: var(--muted-color);
      padding: 0 5px;
      align-self: center;
    }
    
    .icon {
      visibility: hidden;
      padding-left: 15px;
      user-select: none;
      align-self: center;
    }
    
    .icon i:hover::before {
      color: lightsalmon;
    }
  }
  
  .monacoContainer {
    position: relative;
    display: flex;
    flex-grow: 1;
    height: 100%;
    padding: 5px 0 2px 0;
  }
  
  .monacoGreycat {
    flex-grow: 1;
    width: 100%;
    height: 100%;

    .monaco-menu-container .action-item:not(.disabled):hover {
      background-color: #262626;
    }
  }
  
  .no-model {
    position: absolute;
    display: flex;
    height: 100%;
    width: 100%;

    h2 {
      width: 100%;
      text-align: center;
      align-self: center;
    }
  }
}

.run {
  color: #89d185 !important;
}
.run:hover {
  color: #5da559 !important;
}
.run:active {
  color: #2e7e32 !important;
}