.root {
  display: flex;
  flex-flow: column;
  height: 100%;
  flex-grow: 1;

  .header {
    display: flex;
    height: 29px;
    min-height: 29px;
    background-color: var(--bg-inverse-color);
    border-top: 1px solid var(--border-color);
    justify-content: space-between;

    .titleContainer {
      display: flex;

      .title {
        margin: 0;
        padding: 5px 10px;
        font-size: 16px;
        color: var(--inverse-color);
        align-self: baseline;
      }

      .subTitle {
        color: var(--muted-color);
        padding-left: 0;
        font-size: 14px;
        align-self: baseline;
      }
    }

    .actions {
      display: flex;
      align-self: center;

      .action {
        color: var(--inverse-color);
        user-select: none;
        position: relative;
        padding-right: 8px;
      }
      
      .action:hover {
        cursor: pointer;
        color: var(--hover-color);
      }
      
      .action:active {
        cursor: pointer;
        color: #375a7f;
      }
    }
  }
}