.root {
  overflow: hidden;

  .logsContainer {
    flex-grow: 1;
    overflow-x: auto;
    overflow-y: auto;
    display: flex;

    .logsList {
      margin: 0;
      padding: 0;
      list-style-type: none;
      flex-grow: 1;
  
      .log {
        padding: 4px 10px;
        color: var(--color);
        display: flex;
  
        &.debug,
        &.debug:nth-child(even) {
          color: var(--inverse-color);
          background-color: #375a7f;
        }
  
        &.debug:hover {
          background-color: #2b4764;
        }
  
        &.warn,
        &.warn:nth-child(even) {
          color: var(--inverse-color);
          background-color: #F39C12;
        }
  
        &.warn:hover {
          background-color: #d4860b;
        }
  
  
        &.error,
        &.error:nth-child(even) {
          color: var(--inverse-color);
          background-color: #E74C3C;
        }
        &.error:hover {
          background-color: #e12e1c;
        }
  
        &:nth-child(even) {
          background-color: var(--bg-primary-color);
        }
        
        &:hover {
          background: rgba(0, 0, 0, 0.2);
        }
        
        .time {
          margin: 0;
          display: inline-block;
          width: 120px;
        }
        
        .message {
          margin: 0;
        }
      }
    }
    
    .bottomMarker {
      /* give the marker a width/height in order for scrollIntoView to clip left  */
      height: 2px;
      width: 2px;
    }
  }
}