.studio {
  display: flex;
  flex-grow: 1;
  flex-flow: column;
  width: 100%;
  height: 100%;
  color: var(--primary-color);
  background-color: var(--bg-color);

  .muted {
    color: var(--muted-color);
  }

  .flex {
    display: flex;
  }

  .flex-flow-column {
    flex-flow: column;
  }

  .studioHeader {
    display: flex;
    justify-content: space-between;
    padding: 8px 15px;
    color: var(--secondary-color);
    background-color: var(--bg-third-color);

    .greycatLogo {
      width: 40px;
      height: 40px;
      svg {
        width: 40px;
        height: 40px;
        path {
          fill: var(--secondary-color);
        }
      }
    }
  }

  .header {
    height: 40px;
    display: flex;
    align-items: center;
  }

  .headerTitle {
    display: flex;
    flex-flow: column;
    padding-left: 15px;

    h1 {
      margin: 0;
      font-size: 22px;
      color: var(--primary-color);
    }

    span {
      font-family: monospace;
      font-size: 12px;
    }
  }

  .actions {
    display: flex;

    .action {
      display: flex;
      justify-content: center;
      margin-right: 10px;
      align-self: center;
      width: 35px;
      height: 35px;
      border: 1px solid var(--border-primary-color);
      border-radius: 2px;
      background-color: var(--bg-action);
      user-select: none;
      position: relative;

      svg {
        width: 25px;
        height: 25px;
        align-self: center;

        path {
          fill: var(--primary-color);
        }
      }
    }

    .action:last-child {
      margin-right: 0;
    }

    .action:hover:not(.disabled) {
      cursor: pointer;
      background-color: var(--bg-action);
      border: 1px solid var(--border-color);
    }

    .action.disabled::after {
      content: " ";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background-color: rgba(0, 0, 0, 0.5);
    }

    .action.disabled:hover {
      cursor: not-allowed;
    }

    .action:active:not(.disabled) {
      background-color: var(--bg-action-active);
      border: 1px solid #1c1c1c;
    }
  }

  .studioBody {
    position: relative;
    display: flex;
    flex: 1;
  }

  .main-splitter {
    width: 100%;
  }

  .editorPaneContainer {
    background-color: var(--bg-color);
  }

  .splitter {
    .pane {
      z-index: 0;
      overflow: hidden;
    }

    .pane:last-child {
      z-index: 1;
    }
  }

  .studioLayout {
    display: block;
    height: 100%;
    width: 100%;
    overflow-x: hidden;
    overflow-y: auto;
  }
}

.modalOverlay {
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.workspaceModal {
  h2 {
    margin: 0;
  }

  p {
    width: 100%;
    text-align: justify;
  }
}