
.root {
  padding: 10px;

  .rootDir {
    color: var(--muted-color);
    padding: 0 0 5px 0;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;

    &:hover {
      cursor: pointer;
      color: var(--muted-primary-color);
    }
  }

  .list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    
    .item {
      padding: 5px 8px;
      white-space: pre;

      &.workspace {
        display: flex;
        justify-content: space-between;
  
        &:hover {
          cursor: pointer;
          background-color: var(--bg-secondary-color);

          .delete {
            padding-left: 8px;
            display: block;
          }
        }

        &.active {
          color: var(--primary-color);
          background-color: var(--bg-primary-color);
          font-weight: bold;
        }
  
        .delete {
          display: none;
          align-self: center;
  
          &:hover {
            color: #e74c3c;
          }
        }
      }

      &.new {
        font-style: italic;
        color: var(--muted-color);

        & :hover:not(input) {
          color: var(--muted-primary-color);
        }
  
        .input {
          font-size: 15px;

          &span {
            color: var(--muted-color);  
          }

          &input {
            color: var(--muted-primary-color);
          }
        }
      }
    }
  }
}