.section {
  min-height: auto;

  .headerContainer {
    display: flex;
    justify-content: space-between;
    padding: 5px 8px 5px 12px;
    background-color: var(--bg-third-color);
    border-top: 1px solid var(--border-color);
    user-select: none;
    box-shadow: none;

    &:hover {
      background: rgba(0, 0, 0, 0.2);
      cursor: pointer;
    }

    &:active {
      background: rgba(0, 0, 0, 0.3);
    }

    &.open {
      box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
    }

    .header {
      display: flex;

      .icon {
        min-width: 30px;
        text-align: left;
      }

      .title {
        margin: 0;
        font-size: 15px;
        flex-shrink: 0;
      }
    }
  }

  .hide {
    display: none;
  }
}