.root {
  .wsForm {
    display: flex;
    flex-flow: column;
    padding: 8px;

    .inputGroup {
      display: flex;
      justify-content: space-between;

      label {
        color: var(--secondary-color);
        display: block;
        margin-bottom: 2px;
        font-size: 14px;
        min-width: 100px;
      }
      
      input {
        width: 100%;
        margin-bottom: 10px;
        align-content: flex-end;
      }
      
      input[type="checkbox"] {
        width: auto;
      }
    }
    
    button {
      width: 100px;
      margin: auto;
    }
  }
  
  .scripts {
    padding: 8px;
  }
}